<template>
    <div class="container record">
        <div class="inner">
            <h2 class="page_title">운영 이력 관리</h2>
            <!-- content_box -->
            <div class="content_box">
                <!-- input_group -->
                <div class="input_group">
                    <div class="input_box mr30">
                        <select class="select" v-model="searchUsrId">                            
                            <option value="">운영자ID</option>
                            <option
                                v-for="(user,userIdx) in getIntdUserList"
                                :key="userIdx"
                                :value="user.USER_ID"
                            >
                                {{user.USER_NM}}  <!--({{user.USER_ID}})-->
                            </option>
                        </select>
                    </div>
                    <div class="input_box mr30">
                        <select class="select" v-model="searchChgDtaCode">
                            <option value="">구분</option>
                            <option 
                                v-for="(chgData,chgIndx) in getChgDataCodeList"
                                :key="chgIndx"
                                :value="chgData.code">
                                {{chgData.name}}
                            </option>
                        </select>
                    </div>
                    <div class="input_box">                        
                        <datepicker 
                            :language="lang" 
                            :format="customFormatter" 
                            :value="startDate"
                            v-model="startDate"
                            class="w20p ml10" 
                            style="font-size:16px"
                            ref="startDatePicker"
                            :inline = "false"
                            calendar-button-icon="btn-calendar"
                            :calendar-button="false">
                        </datepicker>
                    </div>
                    <span class="date_divide">
                        ~
                    </span>
                    <div class="input_box mr6">
                        <datepicker 
                            :language="lang" 
                            :format="customFormatter" 
                            :value="endDate"
                            v-model="endDate"
                            class="w20p ml10" 
                            style="font-size:16px"
                            ref="endDatePicker"
                            :inline = "false"
                            calendar-button-icon="btn-calendar"
                            :calendar-button="false">
                        </datepicker>
                    </div>
                    <div class="input_box">
                        <button class="btn btn_lg btn_primary btn_lg_txt" @click="fnSearch">정렬</button>
                    </div>
                </div>
                <!-- // input_group -->
                <!-- 이벤트이력 -->
                <div class="fixed_table_box record_table">
                    <div class="table_hd">
                        <!-- 헤더고정 테이블의 경우 헤더와 바디 두개의 테이블의 colgroup 선언을 동일하게 해주세요. -->
                        <!-- 헤더고정테이블의 2개 줄이 필요한 경우가 있습니다. 그럴땐 tr 에 클래스명 table_hd_category를 추가해주세요. -->
                        <table>
                            <colgroup>
                                <col class="w5p">
                                <col class="w10p">
                                <col class="w10p">
                                <col class="w10p">
                                <col class="w10p">
                                <col class="w55p">
                            </colgroup>
                            <tbody>
                                <tr class="table_hd_category">
                                    <td rowspan="2"><span>순번</span></td>
                                    <td colspan="5"><span>돌발상황매니저 사용자 이력</span></td>
                                </tr>
                                <tr>
                                    <td><span>CCTV ID</span></td>
                                    <td><span>조치일시</span></td>
                                    <td><span>구분</span></td>
                                    <td><span>운영자 ID</span></td>
                                    <td><span>처리내용</span></td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div class="table_bd scroll_box">
                        <table>
                            <colgroup>
                                <col class="w5p">
                                <col class="w10p">
                                <col class="w10p">
                                <col class="w10p">
                                <col class="w10p">
                                <col class="w55p">
                            </colgroup>      
                            <tbody>
                                <tr
                                    v-if="hisList.length == 0"                                    
                                >
                                    <td colspan="6" class="txt_c"><span>조회된 이력 내역이 없습니다.</span></td>
                                </tr>    
                                <template
                                    v-if="hisList.length > 0"
                                >
                                    <tr 
                                        v-for="(hisObj,hisIndex) in hisList"
                                        :key=hisIndex
                                    >
                                        <td><span>{{hisObj.RNUM}}</span></td>
                                        <td @click="onMangerPopup(hisObj.CCTV_ID)"><span>{{hisObj.CCTV_ID}}</span></td>
                                        <td><span>{{hisObj.CHANGE_DT}}</span></td>
                                        <td v-if="hisObj.CHANGE_DTA_CODE =='C'"><span>CCTV</span></td>
                                        <td v-if="hisObj.CHANGE_DTA_CODE =='E'"><span>이벤트</span></td>
                                        <td v-if="hisObj.CHANGE_DTA_CODE =='V'"><span>환경설정</span></td>
                                        <td><span>{{hisObj.USER_NM}}</span></td>
                                        <td><span>{{hisObj.CHANGE_CN}}</span></td>
                                    </tr>
                                </template>
                            </tbody>          
                        </table>
                    </div>
                </div>
                <!-- 이벤트이력 -->
            </div>
        </div>
        <PopupCctv
            :isOpen="isContorlPopupOpen"
            :cctvId="selectedCctvId"
            @onClose="onManagerPopupClose"
        />
    </div>
</template>
<script>
import { mapState, mapMutations, mapActions } from 'vuex'
import {ko ,en} from 'vuejs-datepicker/dist/locale';
import Datepicker from 'vuejs-datepicker'
import moment from "moment";
import PopupCctv from '@/components/cctv/PopupCctv'

export default {
    data(){
        return {
            hisList : []
        ,   startDate : new Date
        ,   endDate : new Date
        ,   searchUsrId : ""
        ,   searchChgDtaCode : ""
        ,   lang : ko
        ,   intdCode : this.$store.state.auth.insttId
        ,   intdUserList : this.$store.state.his.intdUserList
        ,   isContorlPopupOpen : false
        ,   selectedCctvId:null
        }
    },
    components : {
        Datepicker,
        PopupCctv
    },
    computed :{
        getChgDataCodeList(){
            return [
                {code : "C" , name : "CCTV"}
            ,   {code : "E" , name : "이벤트"}
            ,   {code : "V" , name : "환경설정"}
            ]
        },
        getIntdUserList(){
            return this.$store.state.his.intdUserList
        }
    },
    watch : {
        startDate(newVal, bfVal)
        {   
            if(newVal > this.endDate)
            {
                this.$refs.startDatePicker.setDate(bfVal)
            }            
        }
    ,   endDate(newVal, bfVal)
        {   
            if(newVal < this.startDate)
            {
                this.$refs.endDatePicker.setDate(bfVal)
            }            
        }
    },    
    mounted(){ 
        $(".fixed_table_box .table_bd").mCustomScrollbar({
            theme : "minimal-dark"
        });
       this.fnSearch(); 
    },
    methods : {
        ...mapActions('his', ['searchHisotryList'])  
    ,   onMangerPopup(cctv_id) {            
            this.isContorlPopupOpen=true
            this.selectedCctvId = cctv_id
        }
    ,   onManagerPopupClose(v) {
            this.isContorlPopupOpen=false
        }      
    ,   customFormatter(date) 
        {
            // 달력 형식 지정 
            return moment(date).format('YYYY-MM-DD');
        }
    ,   async fnSearch()
        {   
            var params ={
                startDate : this.customFormatter(this.startDate)
            ,   endDate : this.customFormatter(this.endDate)
            ,   searchUsrId : this.searchUsrId
            ,   searchChgDtaCode : this.searchChgDtaCode
            ,   intdCode : this.intdCode
            ,   userIsSearch : (this.getIntdUserList.length > 0)? true : false
            }
            const result = await this.searchHisotryList(params);
            //console.log(result.data.dataList)
            this.hisList = result.data.dataList
        }
    }
}
</script>


